export default {
    data() {
        return {
            languageValue: true,
            message: "",
            address: "",
            spaces: [],
        };
    },
    created() {
        this.load();
    },
    methods: {
        changeLanguage() {
            if (this.languageValue) {
                this.$i18n.locale = "zh"
            } else {
                this.$i18n.locale = "en"
            }

        },
        load() {
            this.connect();
        },
        checkChainId() {
            let chainId = window.ethereum.providerMap.get("MetaMask").chainId;
            chainId = this.$web3.utils.hexToNumber(chainId);
            if (chainId !== 84531) {
                this.message = "请先切换到 Goerli 测试网";
                this.warn();
                return false;
            } else {
                this.message = ""
                // this.success();
                return true;
            }
        },
        connect() {
            if (window.ethereum) {
                if (window.ethereum.providerMap.get("MetaMask")) {
                    window.ethereum.providerMap.get("MetaMask").enable().then((res) => {
                        this.address = res[0];
                        let chainIdInvalid = this.checkChainId();
                        if (chainIdInvalid) {
                            document.getElementById("wallet").style.display = "none";
                            document.getElementById("user").style.display = "block";
                        }
                    });
                } else {
                    this.message = "wallet connect fail"
                    this.warn();
                }
            } else {
                this.message = "no wallet"
                this.warn();
            }

        },
        errorHandler() {
            return true
        },
        warn() {
            this.$message({
                message: this.message,
                type: 'warning'
            });
        },
        success() {
            this.$notify({
                title: '成功',
                message: this.message,
                type: 'success'
            });
        }
    },
}
