import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

export default new VueRouter ({
    routes: [
        {
            path: "/",
            component: () => import('@/views/notFind'),
        },
        {
            path: "/spaces",
            component: () => import('@/views/space/spaceShow/spaceShow.vue'),
        },
        {
            path: "/space/*",
            component: () => import('@/views/space/spaceDetail/spaceDetail.vue'),
        }
    ]
})
