import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex)

// 仓库存储数据的到地方，也就是状态
const state = {
    // 登陆状态
    loginSuccess: false,
    //存放Token
    token: '',
    // 断网状态
    networkState: false,
}

// 修改status的唯一手段
const mutations = {
    loginSuccess(state, isSuccess){
        state.loginSuccess = isSuccess;
    },
    changeNetWork(state, netState) {
        state.networkState = netState;
    },
    addToken(state, newToken) {
        state.token = newToken;
    }
}

// 处理actions，可以自己书写自己的业务逻辑，也可以处理异步
const action = {};

// 理解为计算属性，用于简化仓库数据，让组建获取仓库的数据更加方便
const getter = {};

export default new Vuex.Store({
    state,
    mutations,
    action,
    getter
})